* {
    background: rgb(237, 243, 245);
    color: black;
    text-align: center;
    font-family: cursive;
}

div {
    margin: 10px;
}

button {
    width: 15vh;
    margin: 5px;
    padding: 10px;
    border-radius: 60px;
    font-weight: bold;
    border: none;
}

button:hover {
    background: lightblue;
    color: #2353ff;
}

.todos {
    width: 60vh;
    margin: auto;
    overflow: auto;
    height: 50vh;
}
.todos::-webkit-scrollbar {
    background: red;
    width: 0%;
}

.todo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 20px;
    height: 10vh;
    box-shadow: 0px 0px 5px 0px rgb(157, 149, 149);
    border-radius: 10px;
}

.todo .left {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 85%;
    margin: 0;
}

.todo .left input{
    width: 30px;
    height: 30px;
    margin-left: 10px;
}

.todo .left p {
    width: 100%;
    margin: 5px;
    font-size: 20px;
    text-align: left;
}

.todo .right {
    float: right;
    display: flex;
    justify-content: flex-start;
    width: 5%;
    font-size: 20px;
    margin-left: 4%;
    cursor: pointer;
}
.new-task{
    width: 45vh;
    background: #2353ff;
    height: 8vh;
    border-radius: 50px;
    color: white;
    margin: auto;
}
.new-task input{
    background: none;
    border: none;
    width: 28vh;
    height: 5vh;
    margin: 0.7em;
    color: white;
    text-align: left;
    font-size: 15px;
}
.new-task input::placeholder{
    color: white;
    font-size: 15px;
    font-weight: bold;
    text-align: center;
}
.new-task input:focus{
    outline: none;
} 
.new-task i{
    background: #2353ff;
    color: white;
    float: right;
    margin: 5%;
    cursor: pointer;
}
.logout button{
    width: 12vh;
}
.logout button:hover{
    background:red;
    color: white;
}

