
img {
    width: 80px;
    border-radius: 50%;
    padding: 12px;
    box-shadow: 0 2px 6px #001a348a;
    background: white;
}

.login {
    width: 400px;
    font-size: 16px;
    position: absolute;
    left: 50%;
    top: 50%;
    box-shadow: 0 0px 30px 5px black;
    transform: translate(-50%, -50%);
    transition: 1s;
    border-radius: 30px;
}
.login-container {
    background: white;
    padding: 33px 30px;
    transition: 1s;
    border-radius: 25px;
    text-align: center;
}
.input-container {
    opacity: 1;
    transition: 1s;
}

.login-header,
.login p {
    margin-top: 0;
    margin-bottom: 0;
}
.login p{
    padding: 12px;
    background: white;
}

.login input,
.btn {
    box-sizing: border-box;
    display: block;
    width: 100%;
    border-width: 1px;
    border-style: solid;
    padding: 16px;
    outline: 0;
    font-family: inherit;
    font-size: 0.95em;
}

.login input[type="text"],
.login input[type="email"],
.login input[type="password"] {
    background: #fff;
    border-color: #bbb;
    color: rgb(0, 0, 0);
    border: none;
    background-color: transparent;
    border-bottom: 1px solid #c0c0c03b;
    padding-left: 0;
    padding-bottom: 10px;
}

.login input[type="email"]:focus,
.login input[type="password"]:focus {
    border-color: #888;
}

.btn{
    background: rgb(17, 79, 141);
    width: 140px;
    margin: auto;
    border-bottom: 22px;
    border: none;
    color: white;
    border-radius: 22px;
    cursor: pointer;
    margin-top: 12px;
}
.btn:hover{
    background-color:rgb(12, 105, 199) ;
    font-weight: bold;
    color: white;
}
.a-btn{
    color: rgb(12, 105, 199);
    text-decoration: none;
    background: white;
}
.a-btn:hover{
    font-weight: bold;
}